
* {
  margin: 0;
  padding: 0;
  border: none;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}

i {
  font-style: normal;
}

/**
/  3840 * 2160

   4880 * 1440
 */
body {
  font-size: 0.18rem;
  font-family: "MicrosoftYaHei Blod", MicrosoftYaHei, "Microsoft Sans Serif", Verdana, Arial, Helvetica, sans-serif;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  -ms-user-select: none;
}

td,
th,
caption {
  font-size: 0.18rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: 100%;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

a {
  color: #555;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

img {
  border: none;
}

ol,
ul,
li {
  list-style: none;
}

input,
textarea,
select,
button {
  font: 0.18rem Verdana, Helvetica, Arial, sans-serif;
  border: none;
}

table {
  border-collapse: collapse;
}

/* 清浮动 */
.clearFix:after {
  content: "\200B";
  display: block;
  height: 0;
  clear: both;
}

.clearFix {
  *zoom: 1;
}

/* 浮动 */
.fl {
  display: block;
  float: left;
}

.fr {
  display: block;
  float: right;
}

.anchorBL {
  display: none
}

/*滚动条美化*/
::-webkit-scrollbar { /*滚动条宽度*/
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
  border-radius: 4px;
  box-shadow: inset 0 0 5px rgba(13, 123, 255, .7);
  background: rgba(13, 123, 255, .5);
}

::-webkit-scrollbar-track { /*滚动条里面轨道*/
  background: transparent;
}

.scroll-bar::-webkit-scrollbar { /*滚动条宽度*/
  width: 8px;
  height: 8px;
}

.scroll-bar::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
  border-radius: 4px;
  box-shadow: inset 0 0 5px rgba(13, 123, 255, .7);
  background: rgba(13, 123, 255, .5);
}

.scroll-bar::-webkit-scrollbar-track { /*滚动条里面轨道*/
  background: transparent;
}

