.rotate_component_root {
  /*position: relative;*/
  width: calc(1285px * 0.8);
  height: calc(875px * 0.8);
  position: absolute;
  background: url(/static/media/rotateicons-bg.096452ec.png) no-repeat center 5px;
  /*border: 1px solid #000;*/
  /*background-size: 70% 80%;*/
}

/*track*/
.rotate_component_root .rotate_component_track_wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: rotate3d(2, 0, 1, 60deg);
          transform: rotate3d(2, 0, 1, 60deg);
}

.rotate_component_track_wrapper .rotate_component_track {
  position: absolute;
  border: 2px solid #12ffff;
  border-radius: 50%;
  width: calc(1080px * 0.65);
  height: calc(1080px * 0.65);
  -webkit-transform-origin: center;
          transform-origin: center;
  left: 95px;
  /*top: 200px;*/
  -webkit-animation: rotateComponentAnimate 5s infinite forwards linear;
          animation: rotateComponentAnimate 5s infinite forwards linear;
}

.rotate_component_track_wrapper .rotate_component_track_point {
  position: absolute;
  left: 50%;
  top: -5px;
  width: 20px;
  height: 10px;
  background: #12ffff;
  border-radius: 50%;
}

@-webkit-keyframes rotateComponentAnimate {
  from {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
  }
  to {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
  }
}

@keyframes rotateComponentAnimate {
  from {
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
  }
  to {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
  }
}

/*rotate-innerTrack*/
.rotate_component_root .rotate_component_path_word_root {
  position: absolute;
  width: auto;
  height: auto;
  left: 60px;
  top: 170px;
}

/*rotate-elements*/
.rotate_component_root .rotate_component_ellipse {
  position: absolute;
  width: calc(1080px * 0.65);
  height: calc(650px * 0.65);
  /*border: 1px solid #f00;*/
  border-radius: 50%;
  left: 0;
  top: 0;
  -webkit-transform: translate(150px, 55px) rotate(15deg);
          transform: translate(150px, 55px) rotate(15deg);
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  background: transparent;
}

.rotate_component_ellipse .item {
  position: absolute;
  /*left: calc(45px / 2);*/
  /*top: calc(440px / 2);*/
