.tooltip-box {
    position: relative;
    padding: 10px 20px;
    font-size: 16px;
    line-height: 16px;
    color: #fff;
    border-radius: 5px;
    border: 1px solid #12ffff;
    border-left: 0;
    display: inline-block;
    background-color: rgba(12,24,73,.8);
}
.tooltip-box p {
    color: #fff;
    text-align: left;
    margin-top: 10px;
    font-size: 14px;
}
.tooltip-box p:first-child {
    margin-top: 0;
}
.tooltip-box .lt,
.tooltip-box .rb {
    position: absolute;
    left: -1px;
    right: 50%;
    border: 1px solid #12ffff;
}

.tooltip-box .lt {
    border-top-left-radius: 5px;
    top: -1px;
    bottom: calc(50% + 8px);
    border-right: 0;
    border-bottom: 0;
}

.tooltip-box .rb {
    border-bottom-left-radius: 5px;
    bottom: -1px;
    top: calc(50% + 8px);
    border-top: 0;
    border-right: 0;
}
.tooltip-box .angle {
    display: block;
    width: 0;
    height: 0;
    border-left: 6px solid rgba(12,24,73,.8);
    border-bottom: 6px solid rgba(12,24,73,.8);
    border-top: 6px solid transparent;
    border-right: 6px solid transparent;
    -webkit-transform: rotateZ(45deg);
            transform: rotateZ(45deg);
    position: absolute;
    top: 50%;
    left: -6px;
    margin-top: -6px;
}
.tooltip-box .angle::after,.tooltip-box .angle::before {
    content: "";
    display: block;
    background-color: #12ffff;
    position: absolute;
}
.tooltip-box .angle::after {
    bottom: -6px;
    left : -6px;
    height: 12px;
    width: 1px;
}
.tooltip-box .angle::before {
    bottom: -6px;
    right: -6px;
    height: 1px;
    width: 12px;
}
.tooltip-box .lt::after,
.tooltip-box .rb::after {
    content: "";
    display: none;
}

